.App {
  
  text-align: right;
  padding: 10px 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.inputBuscar {
  width: 40% !important;
  
}

table {
  min-width: 500px;
}

.containerInput {
  align-items: end;
  right: 0;
  display: flex;
  justify-content: inherit;
  padding-bottom: 25px;
}

@media (max-width: 988px) {
  .inputBuscar {
    width: 100% !important;
  }

  .App {
    padding: 40px 5px;
  }

  .containerInput {
    padding-bottom: 15px;
  }
}
